import React from 'react';
import Navbar from '../components/navbar'; 
import styles from '../styles/dashboard.module.css';
import Link from 'next/link';
import { useRouter } from 'next/router';

const DashboardPage: React.FC = () => {

  const router = useRouter();

  useEffect(() => {
    // Check if token exists in localStorage
    const token = localStorage.getItem('token');
    if (!token) {
      // Redirect to login if token is missing
      router.push('/login');
    }
  }, [router]);

  // Render dashboard content only if token exists
  return (
    <div className={styles.dashboardContainer}> {/* Wrap in dashboardContainer */}
      <Navbar /> {/* Add Navbar component */}
      
      <div className={styles.container}>
        <h1 className={styles.title}>Welcome to Your Dashboard</h1>
        
        <div className={styles.metrics}>
          <p>Your sustainability metrics will be displayed here.</p>
        </div>
        
        <div className={styles.navigation}>
          <Link href="/login">
            <button onClick={() => localStorage.removeItem('token')}>
              Logout
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage